#root{
  background-image: linear-gradient(52deg, #ceebc8 0%, #88859f 70%);
  
  
}

*{
  font-family:Arial, Helvetica, sans-serif;

}
.navb{
  transition-property: font-size;
  transition-duration: .15s;
}

.navb:hover{
  font-size: 1.1rem;
}

.container1{
  display: flex ;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 10rem auto;
  text-align: center;
  color: rgb(10, 11, 11);
}
.container1::before{
  content: '';
  background: url(/home1.webp) no-repeat center center/cover ;
  position: absolute;
  width: 100%;
  height: 35rem;
  z-index: 0;
  top:0;
  opacity: .15;
}

.container2{
  display: flex ;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 5rem;
  text-align: center;
  color: rgb(8, 9, 9); 
}
.container2 h1{
  margin-top: 10rem;
}

.card-text{
  text-align:justify;
}

