.container3{
  margin-top: 5rem;
  
}
.container3 h1, .container2 h1{
  transition-property: all;
  transition-duration: 2s;
  transition-delay: 1s;
}

.container3 h1:hover, .container2 h1:hover{
  background-image:linear-gradient(to right, rgba(255,0,0,0), rgb(123 145 157));
  border-radius: 10px;
}
.me1{
  display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}
  

.me{
  margin-left: 5%;
  margin-top: 10%;

}
.me h1{
  font-size: 5rem;
  color: black;
  margin-bottom: 20px;
}
.me span{
  color: antiquewhite;
}
.me p{
  color: #555;
  line-height: 22px;
}
.me a{
  background-color:#555;
  padding: 10px 18px;
  text-decoration: none;
  font-weight: bold;
  color: #ceebc8;
  display: inline-block;
  margin: 30px 0px;
  border-radius: 5px;
}


.images img{
  width: 22rem;
  height: 22rem;
  border-radius: 12rem;
  transition-property: all;
  transition-duration: .12s;

}
.images img:hover{
  width: 22.5rem;
  height: 22.5rem;
}

.container4{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 2.5rem;
  /* background-color: #88859f; */
  font-size: 1.5rem;
}
.logo img{
  margin: 3px 7px;
}
.mail{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}



@media(max-width:500px){
  
  .container1 {
    align-items: center;
    color: #0a0b0b;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin-top: 8rem;
  }

  .container1 h4{
    font-size: 2rem;
  }
  .container2 {
    align-items: center;
    color: #080909;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 0rem;
    text-align: center;
  }

  }
